//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//START GraphikLCG
@font-face {
	@include fontdefault(normal, 900, 'GraphikLCG');
	@include fontpath('GraphikLCG Super', 'GraphikLCG-Super', GraphikLCG-Super); }
@font-face {
	@include fontdefault(italic, 900, 'GraphikLCG');
	@include fontpath('GraphikLCG Super Italic', 'GraphikLCG-SuperItalic', GraphikLCG-SuperItalic); }

