body {
  color: #333;
  font-family: "GraphikLCG", "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.6;
  background: #fff;
}

.out {
  position: relative;
  overflow: hidden;
}

.inner {
  margin: 0 auto;
  padding: 0 40px;
  max-width: 1600px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.block {
  display: flex;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding-top: 40px;
  color: #fff;
  z-index: 100;
}

.logo {
  float: left;
}

.nav {
  float: left;
  margin-left: 167px;
  font-size: 16px;
  font-weight: 700;
}

.nav__item {
  vertical-align: top;
  display: inline-block;
}

.nav__item:not(:first-child) {
  margin-left: 24px;
}

.nav__link {
  opacity: .6;
  color: #fff;
  transition: opacity .3s ease-in-out;
}

.no-touch .nav__link:hover {
  opacity: 1;
}

.lang {
  position: relative;
  float: right;
  padding-right: 12px;
  min-width: 12px;
  font-size: 16px;
  font-weight: 700;
}

.lang:before {
  position: absolute;
  content: "";
  top: 50%;
  right: 0;
  margin-top: -2px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4px 3.5px 0 3.5px;
  border-color: #fff transparent transparent transparent;
}

.lang__list {
  position: absolute;
  top: 100%;
  width: 42px;
  left: 0;
  display: none;
}

.lang__item {
  display: block;
}

.topic {
  height: 100vh;
  background-image: linear-gradient(138deg, #2D3353 9%, #3E3E88 70%);
}
